<template>
    <div>
        <el-form 
            style="margin-top: 100px ;max-width: 400px; margin-left: auto; margin-right: auto;" :model="loginForm"
            status-icon label-width="auto" class="demo-ruleForm">
            <el-form-item :label="$t('Tài khoản')">
                <el-input v-model="loginForm.UserName" type="text"  clearable />
            </el-form-item>
            <el-form-item :label="$t('Mật khẩu')">
                <el-input v-model="loginForm.PassWord" type="password" autocomplete="off" clearable />
            </el-form-item>
            <el-form-item>
                <el-row :gutter="24">
                    <el-col :span="12">
                        <el-button type="primary"  plain size="large" @click="Login()"><el-icon>
                                <Unlock />
                            </el-icon> &nbsp;{{ $t('Đăng nhập') }}</el-button>
                    </el-col>
                    <el-col :span="12">
                        <el-button type="warning" size="large" plain><el-icon>
                                <Promotion />
                            </el-icon>&nbsp;{{ $t('Đăng nhập với Smart Office') }}</el-button>
                    </el-col>
                </el-row>

            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import { ElMessage } from 'element-plus'
import api from "@/model/api.js"
export default {
    name: "LoginPage",
    data() {
        return {
            loginForm: {
                UserName: "",
                PassWord: "",
            },
        }
    },
    methods: {
        Login() {
            api.CheckLogin(this.loginForm, (dat) => {
                if (dat.data.status == "success") {
                    ElMessage({
                        message: dat.data.message,
                        type: 'success',
                    })
                    this.$store.commit("setEmpInfor", dat.data.data);
                    this.$router.push({ name: 'Welcome', params: {} })
                }
                else {
                    ElMessage.error(dat.data.message)
                }
            }, (er) => {
                 ElMessage.error(er.message)
            })
        },

    }

}
</script>
<style scoped></style>